*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.App{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgb(80, 80, 214);
  height: 100vh;

}
h1{
  margin-bottom: 10px;
}
#temperature{
  font-size: 3rem;
}
button{
  background-color: rgb(210, 129, 210);
  border: none;
  padding: .5rem 2rem;
  transition: all 0.2sec ease-in;
}
button:hover{
  background-color:  rgb(212, 174, 212);
}
